<template>
    <div>
        <div class="row searchBar fixed-top">
        
        <div class="col-sm-12">
            <img class='logo' src="../assets/logo.png" alt="">
        </div>

        <div class="col-sm-12">            
            <input class='search' type="search" @input='textChange' placeholder='Поиск' v-model="searchString" aria-label="Поиск">
        </div>
        
        
    </div>

    <div v-if='items.length>0' class="row">
        <Packets :items="items"></Packets>
    </div>
    </div>
    
</template>

<script>
const axios = require('axios').default;

axios.defaults.headers.post['Content-Type'] ='application/json;charset=utf-8';
axios.defaults.headers.post['Access-Control-Allow-Origin'] = '*';
import Packets from '@/components/Packets.vue'

export default {
    name: 'Search',
    components: {
        Packets
    },
   
    data(){
         return{
           searchString: '',
           items:{
                name: '',                
           }
         }
    },
    methods:{
        addItem(item){
            console.log('Пытаемся добавить ' + item);
            this.items={};
            this.items.push({
                name: item
            });
        },
        textChange(){     
        
        
            //this.addItem('123')  
            //console.log(this.searchString);

            axios.post('http://ovz1.kenodo42.gmzem.vps.myjino.ru:49164/search/', {
                searchString: this.searchString,
            })
            .then(response => {
                console.log(response);
                this.items=response.data;          
            })
            .catch(function (error) {
                
                console.log(error);
            });
        }

        
    },

}
</script>

<style scoped>
.logo{
    height:50px;
    margin-top:3px;
    mix-blend-mode: darken;
}

.search{
    margin-top: 6px;
    border: 2px solid #474246;
    border-radius: 10px;
    text-align: center;
    padding: 5px;
    font-weight: 800;
    font-size:15pt;
    width:200px;
}

.menu-burger{
    margin-top: 13px;
    font-size: 21pt;
}

.searchBar{
    background-color: #f3f3f5;
    padding-bottom:5px;
}
</style>